.nav-bar {
  /* border: 1px solid grey; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: var(--margin-outer);
  padding-bottom: 50px;
}

.logo{
  object-fit: contain;
  height: 10.5vh;
}

.links-cont {
  border: 1px solid aquamarine;

  position: absolute;

  display: flex;
  flex-direction: column;
  align-items: flex-end;

  margin-top: 45px;

}

/* -----morphing menu---- */
label .hamburger {
  position: absolute;
  top: 135px;
  left: 50px;
  width: 30px;
  height: 2px;
  background: var(--color-calypso);
  display: block;
  -webkit-transform-origin: center;
  transform-origin: center;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
}

label .hamburger:after, label .hamburger:before {
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: var(--color-calypso);
}

label .hamburger:before { top: -10px; }
label .hamburger:after { bottom: -10px; }
label input { display: none; }
label input:checked + .menu {
  box-shadow: 0 0 100vw var(--color-turqouise), 0 0 0 100vh var(--color-turqouise);
  background-color: var(--color-turqouise);
  /* border-radius: 0; */
  border-radius: 50% 50% 50% 50%;
}

label input:checked + .menu .hamburger {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
label input:checked + .menu .hamburger:after {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  bottom: 0;
}
label input:checked + .menu .hamburger:before {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  top: 0;
}
label input:checked + .menu + ul { opacity: 1; }

/* full screen */
label .menu {
  position: absolute;
  right: -100px;
  top: -100px;
  z-index: 100;
  width: 200px;
  height: 200px;
  background: #FFF;
  background: var(--color-atlantis);
  /* background-image: linear-gradient(to bottom left, #8bd2c46f,white, #8bd2c46f); */
 
  border-radius: 50% 50% 50% 50%;
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  box-shadow: 0 0 0 0 #FFF, 0 0 0 0 #FFF;
  cursor: pointer;
}
label ul {
  z-index: 200;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 0;
  -webkit-transition: .25s 0s ease-in-out;
  transition: .25s 0s ease-in-out;
 
}
label a {
  margin-bottom: 1em;
  display: block;
  color: #000;
  text-decoration: none; 
}


 /* ---END morphing menu--- */

